import { Component, OnInit, NgZone } from '@angular/core';
import { ReportsCountryServiceService } from '../_services/reports-country-service.service';
import { country } from '../_modules/Country';
import * as AOS from 'aos';
import { MatDialog } from '@angular/material/dialog';
import { async } from '@angular/core/testing';
import { __await } from 'tslib';
@Component({
  selector: 'app-table-main',
  templateUrl: './table-main.component.html',
  styleUrls: ['./table-main.component.css']

})
export class TableMainComponent implements OnInit {

  dataSource: country[];
  ModeDisplay = 'Medium';
  StateDisplaying = true;
  errorInternet = false;
  sortMode: any;
  termAsearching: string;
  constructor(private ReportCountriesInfo: ReportsCountryServiceService, public dialog: MatDialog, private zone: NgZone) { }

  ngOnInit() {
    AOS.init();
    this.ReportCountriesInfo.getreportcountries().then(response => {
      setInterval(() => this.StateDisplaying = false, 1000);
      setInterval(() =>  this.dataSource = response as country[], 1000);

    }, err => {
      this.StateDisplaying = false;
      this.errorInternet = true;
      this.dialog.open(Alertfield);
    }).catch();
  }
  // sort
  sorting(SortMode: string) {

    switch (SortMode) {
      case 'Confirmed':
        this.srt('cases');
        this.scrollToTop();
        break;
      case 'Deaths':
        this.srt('deaths');
        this.scrollToTop();
        break;
      case 'Recovereds':
        this.srt('recovered');
        this.scrollToTop();
        break;
    }
  }
  srt(field) {
    this.dataSource.sort((a: any, b: any) => {
      if (a[field] > b[field]) {
        return -1;
      } else if (a[field] < b[field]) {
        return 1;
      } else {
        return 0;
      }
    });
    return this.dataSource;
  }
  scrollToTop() {
    window.scroll({
      top: 0,
      left: 0,
      behavior: 'smooth'
    });
  }
  displaying(dispalyMode: string): string {
    return this.ModeDisplay = dispalyMode;
  }
}
@Component({
  // tslint:disable-next-line: component-selector
  selector: 'Alertfield',
  templateUrl: 'Alert-field.html',
})
// tslint:disable-next-line: component-class-suffix
export class Alertfield { }
