import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { ActivatedRoute } from '@angular/router';
import { country } from '../_modules/Country';
import { __await } from 'tslib';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ReportsCountryServiceService {

  constructor(private route: ActivatedRoute, private http: HttpClient) { }
  baseUrl = 'https://corona.lmao.ninja/v2/countries?yesterday=0';
  currentCountry: country;
 async getreportcountries() {
    const res = await this.http.get(this.baseUrl).toPromise();
    return res;
   }
  // getReportCountries(): Observable<country[]> {
  //   return  this.http.get<country[]>(this.baseUrl);
  // }
  // tslint:disable-next-line: no-shadowed-variable
  async getStatisticCountry(country: string) {

    const url = 'https://corona.lmao.ninja/v2/historical/' + country + '?lastdays=30';
    return await this.http.get(url).toPromise();
  }
  // tslint:disable-next-line: no-shadowed-variable
  async getCountryInfo(country: string) {
    return this.http.get('https://corona.lmao.ninja/v2/countries/' + country + '?yesterday=false').toPromise();
  }
}

